.login {
    display: flex;
    min-height: 100vh;
    justify-content: center;
    align-items: center;
    background-image: url('../../assets/pic2.webp');
    background-size: cover;
  }
  
  .form-container {
    width: 360px;
    margin: auto;
    /* box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24); */
    padding: 10px;
    border-radius: 30px;
    color: rgb(9, 12, 12);
  }
  
  .login-form {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 10px;
  }
  

  
  .form-field {
    margin: 10px 0 10px 0;
    padding: 15px;
    font-size: 16px;
    border: 0;
    font-family: "Roboto", sans-serif;
  }
  
  input {
    background: #f2f2f2;
  }
  button {
    background: #4caf50;
    color: white;
    cursor: pointer;
  }
  
  button:disabled {
    cursor: default;
  }
  